<template>
	<div class="flex w-full">
		<div class=" mx-auto self-center">
            <a href="http://www.ejoya.co/">
			<img src="@/assets/images/logo/logo.png" alt="login" class="mx-auto form-logo">
			</a>
			<vx-card class="no-box-shadow card-down">
				<div slot="no-body" class="">
					<div class="vx-row">
						<div class="vx-col sm:w-full md:w-full lg:w-1/2 mx-auto self-center bg-white">
							<div class="p-8">
								<div class="vx-card__title mb-8">
									<h1 class="mb-4 font-bold">Login</h1>
									<p class="font-light">Welcome back to Ejoya Music!.</p>
								</div>

							

								<form @submit.prevent="login">

									<vs-input label="What is your Email Address" placeholder="Enter your Email Address" type="email" v-model="emailAddress" class="w-full mb-6"/>

									<vs-input type="password" label="Enter Password" placeholder="Password" v-model="password" class="w-full mb-2" />

									<div class="text-right">
										<router-link class="forgot-link text-black" to="/forgot-password" size="small" color="dark" type="flat">Forgot Password ?</router-link>
									</div>

									<div class="gradient-btn-div">
										<vs-button class="gradient-btnn" :color="themeOne" :gradient-color-secondary="themeTwo" type="gradient">Login</vs-button>
									</div>
									<div class="mt-3 text-center">
										<router-link class="text-black my-2" to="/sign-up" color="dark" type="flat">Don't have an Account? Get Started here!</router-link>
									</div>

								</form>
								
							</div>
						</div>
                        <div class="vx-col hidden sm:hidden md:hidden lg:block lg:w-1/2 mx-auto self-center">
							<img src="@/assets/images/ejoya/ejoya-login.png" alt="login" class="mx-auto login-img">							
						</div>
					</div>
				</div>
			</vx-card>
		</div>
	</div>
</template>

<script>
import http from '../../http';
export default {
	data() {
		return {
			title: 'Ejoya Music',
			error: '',
            themeOne: '#E5396B',
            themeTwo: '#C85B80',
            emailAddress: '',
			password: '',
			checkBox1: false
		}
	},
	mounted(){
	},
	methods:{
		login(){

			let axiosConfig = {
				headers: {
				}
			};

			if (this.validateLogin()) {
				this.$store.dispatch("SET_LOADING", true);
					http
						.post("/login/", {
							"email": this.emailAddress,
							"password": this.password,
						}, axiosConfig)
						.then(response => {
							this.$store.dispatch("SET_LOADING", false);
							this.handleLogin(response);
						})
						.catch(error => {
							this.$store.dispatch("SET_LOADING", false);
							this.handleError(error);
						});
				}	
		},
		validateLogin(){
			if (this.emailAddress == "") {
					this.$swal(this.title, "Email Address is Required !", "info");
					return false;
                }
                if (this.password == "") {
                    this.$swal(this.title, "Password is Required !", "info")
                    return false;
                }
                return true;
		},
		handleLogin(response){
			if (response.status == 200) {
				this.$swal(this.title, "Login Successful !", "success");
				let data = response.data;
				// set User token and User Object
				this.saveUserToStorage(data);

				setTimeout(() => {
					this.$router.push("/dashboard/analytics");
				}, 1000);
			}
		},
		handleError(error){
			if(error.response.status == 400){
				this.$swal(this.title, "Unable to log in with provided credentials.", "danger");
			}
		},
		saveUserToStorage(data) {
			if (window.localStorage) {
				localStorage.setItem("token", JSON.stringify(data));
			}
		}
	}
}
</script>